<template>
    <div class="root">
        <div class="nav-container" ref="navContainer"></div>
        <div class="box">
            <div class="content">
                <div class="main">
                    <div class="write-title"><h3>创作服务平台</h3></div>
                    <div class="top-box">
                        <div class="top-box-left">
                            <div class="article-info">
                                <div class="article-title">
                                    <el-form ref="form" :model="form" label-width="80px" :rules="pubFormRules">
                                        <el-form-item label="标题" class="change-label-calss" prop="title">
                                            <el-input v-model="form.title" placeholder="请书写文章标题"></el-input>
                                        </el-form-item>
                                        <el-form-item label="概述" class="change-label-calss" prop="articledesc">
                                            <el-input v-model="form.articledesc" placeholder="请书写文章概述"></el-input>
                                        </el-form-item>
                                        <el-form-item label="标签" prop="region" class="change-label-calss" style="display:inline-block;">
                                            <el-select v-model="form.type" placeholder="请选择文章标签">
                                            <el-option label="标签1" value="shanghai"></el-option>
                                            <el-option label="标签2" value="beijing"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </div>
                            </div>
                        </div>
                        <div class="top-box-right">
                            <div class="article-pic">
                                <el-upload
                                class="upload-demo"
                                drag
                                action="#"
                                ref="uploadRef"
                                :on-change="onChange"
                                :file-list="picFile"
                                :auto-upload="false"
                                accept=".jpeg, .png, .jpg"
                                :before-upload="beforeUpload"
                                multiple>
                                <i class="el-icon-upload"></i>
                                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                                <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                                </el-upload>
                            </div>
                        </div>
                        <div class="top-middle">
                            <div class="pic-limit">
                                <h2>图片大小/格式</h2>
                                <p>图片上传大小不超过10MB</p>
                                <p>图片上传格式为jpg、png格式</p>
                            </div>
                            <div class="article-limit">
                                <h2>图片分辨率</h2>
                                <p>推荐上传宽高比为3:4、分辨率不低于720*960的照片，超过1280p的图片用网页端上传画质更加清晰</p>
                            </div>
                        </div>
                    </div>

                    <div class="bottom-box">
                        <div class="bottom-info">
                            <el-form ref="form" :model="form" label-width="80px" size="medium" :rules="pubFormRules" prop="content">
                                <quill-editor v-model="form.content"></quill-editor>
                            </el-form>
                        </div>
                    </div>

                    <div class="submitbtn">
                        <el-row>
                            <el-button @click="resetFn()">清空</el-button>
                            <el-button type="primary" @click="pubArticleFn()">提交</el-button>
                            <el-button type="info" @click="pubArticleFn('草稿')">保存草稿</el-button>
                        </el-row>
                    </div>
                </div>
                </div>
        </div>
    </div>
</template>

<script>
import { addArticleInfo } from '@/api'
import { addArticlePic } from '@/api'
import axios from "axios"

export default {
data() {
  return {
    form: {
      title: '',
      articledesc: '',
      content: '',
      authorId: this.$store.state.userInfo.id,
      time:''
    },
    index: 0,
    picFile:[],
    pubFormRules:{
        title: [
            {required: true,message: '请输入文章标题！',trigger: 'blur'},
            {min:1,max:30,message:'文章标题长度为1-30个字符',trigger: 'blur'},
        ],
        articledesc:[
            {required: true,message: '请输入文章简介！',trigger: 'blur'},
            {min:1,max:30,message:'文章简介长度为1-30个字符',trigger: 'blur'},
        ],
        content:[
            {required: true,message: '请输入文章正文！',trigger: 'blur'},
        ]
    }
  }
},
mounted(){
    const time = this.$moment(new Date()).format('YYYY年MM月DD月 HH时mm分ss秒')
    this.form.time = time
},
methods: {
    onChange(file){
        this.picFile[this.index] = file
        this.index ++
        console.log(this.picFile);
    },
    async uploadPic(){
        let file = new FormData()
        this.picFile.forEach((item,index)=>{
            file.append('imgFile',this.picFile[index].raw)
        })
        
        axios({
            url:'http://8.130.20.53:3000/admin/addArticlePic',
            method:'POST',
            headers:{
                'Content-Type': 'multipart/form-data',
                'Authorization': localStorage.getItem('token')
            },
            data: file,
            params:{
                articleinfo: this.form
            }
        }).then(res =>{
                if(res.status == 200){
                    this.$message({
                        type: 'success',
                        message: '图片上传成功',
                    })
                }else{
                    this.$message({
                        type: 'error',
                        message: '图片上传失败'
                    })
                }
            })
    },
    beforeUpload(file) {

    },
    resetFn() {

    },
    async pubArticleFn(){
        if(this.form.title == ''){
            this.$message({
                type: 'warning',
                message: '文章标题不能为空！'
            })
        }else if(this.form.articledesc == ''){
            this.$message({
                type: 'warning',
                message: '文章概述不能为空！' 
            })
        }else if(this.form.content == ''){
            this.$message({
                type: 'warning',
                message: '文章正文内容不能为空！'
            })
        }else{
            const { data: res } = await addArticleInfo(this.form)
            if(res.status !== 0) return this.$message.error(res.message)
            this.uploadPic()
            this.$message({
                type: 'success',
                message: '文章已上传，将在审批后发布到网站中，请耐心等待！'
            })
            // this.reset()
        }

    },

}
}
</script>

<style scoped>
.root{
width: 100%;
/* min-height: 1000px; */
min-height: 100vh;
min-width: 1200px;
/* background: linear-gradient(-45deg, #4b6a99, #03213c, #031225, #3e6192); */
background: url(@/assets/中草药滚动背景.png);
background-repeat: no-repeat;
background-size: 140% 100%;
animation: gradientBG 70s ease infinite;
}
@keyframes gradientBG {
0% {
    background-position: 0% 50%;
}
50% {
    background-position: 100% 50%;
}
100% {
    background-position: 0% 50%;
}
}
.nav-container{
  width: 100%;
  height: 90px;
  /* background-color: #00ff55d6; */
  background-image: url(@/assets/navbg2.png);
  background-size: 100% 100%;
}
.box{
width: 80%;
min-width: 1200px;
margin: 0 auto;
}
.content{
padding: 10px;
position: relative;
z-index: 1;
}
.write-title{
margin-bottom: 30px;
font-size: 20px;
color: #ffffff;
text-align: center;
}
.main{
/* background: rgba(255, 255, 255, 0.49); */
padding: 30px;
margin: 30px auto;
border-radius: 20px;
border: 1px solid #f1ebe5;
box-shadow: 0px 3px 10px rgb(94, 94, 94);;
}
.top-box{
padding-bottom: 30px;
width: 100%;
display: flex;
justify-content: space-between;
}

.change-label-calss /deep/ .el-form-item__label{
color: #ffffff;
font-weight: 500;
font-size: 15px;
}

.top-box-left{
width: 40%;
}
.top-box-right{
width: 35%;
}
.el-upload-dragger .el-upload__text em {
    color: #e0ab61;
    font-style: normal;
}
.el-upload__tip {
    font-size: 12px;
    color: #c0a176;
    margin-top: 7px;
}
.top-middle{
    width: 20%;
    text-align: center;
}
.top-middle h2{
    font-size: 14px;
    margin-top: 10px;
    color: #f1ebe5;
}
.top-middle p{
    font-size: 11px;
    margin-top: 10px;
    color: #f1ebe5;
}
.pic-limit{
    height: 45%;
    border: #ffffff 1px solid ;
    border-radius: 7px;
    padding: 0 5px;
}
.article-limit{
    height: 45%;
    border: #f1ebe5 1px solid ;
    margin-top: 5%;
    border-radius: 7px;
    padding: 0 5px;
}
.article-pic{
padding: 10px;
border: 1px solid #f1ebe5;
border-radius: 7px;
}
.bottom-box{
width: 100%;
margin: 10px auto;
}
.bottom-box .details{
